@use '../util' as *;

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: rem(36);
  gap: rem(24) 0;
  width: 100%;

  @include breakpoint(medium) {
    align-items: center;
  }

  &__title {
    width: 100%;
    justify-self: start;
    @include breakpoint(medium) {
      width: auto;
    }
  }

  &__subtitle {
    font-size: rem(14);
    font-weight: 700;
    color: var(--text-color-secondary);
  }
}
