:root [data-theme=light] {
  --background: hsl(0, 0%, 100%);
  --text-color: hsl(230, 17%, 14%);
  --text-color-secondary: hsl(228, 12%, 44%);
  --card-bg: hsl(227, 47%, 96%);
  --card-bg-hover: hsl(228, 33%, 91%);
  --toggle-bg: hsl(227, 47%, 96%);
  --toggle-button: hsl(0, 0%, 100%);
  --icon-color-sun: var(--text-color);
  --icon-color-moon: var(--text-color-secondary);
  --toggle-button-pos: 4px;
}

:root[data-theme=dark] {
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --background: hsl(230, 17%, 14%);
  --text-color: hsl(0, 0%, 100%);
  --text-color-secondary: hsl(228, 34%, 66%);
  --card-bg: hsl(228, 28%, 20%);
  --card-bg-hover: hsl(228, 25%, 27%);
  --toggle: hsl(0, 0%, 100%);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: hsl(230, 17%, 14%);
  --icon-color-sun: hsla(185, 39%, 25%, 0.76);
  --icon-color-moon: var(--text-color);
  --toggle-button-pos: 2.25rem;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --background: hsl(0, 0%, 100%);
  --text-color: hsl(230, 17%, 14%);
  --text-color-secondary: hsl(228, 12%, 44%);
  --card-bg: hsl(227, 47%, 96%);
  --card-bg-hover: hsl(228, 33%, 91%);
  --toggle-bg: hsl(227, 47%, 96%);
  --toggle-button: hsl(0, 0%, 100%);
  --icon-color-sun: var(--text-color);
  --icon-color-moon: hsla(229, 12%, 44%, 0.65);
  --toggle-button-pos: 4px;
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  --default-transistion: color 0.3s ease-in-out, background 0.3s ease-in-out,
    background-color 0.3s ease-in-out, border-color 0.3s ease-in-out,
    fill 0.3s ease-in-out, stroke 0.3s ease-in-out, opacity 0.3s ease-in-out,
    transform 0.3s ease-in-out;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
  transition: var(--default-transistion);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
  gap: 1.5rem 0;
  width: 100%;
}
@media (min-width: 40em) {
  .header {
    align-items: center;
  }
}
.header__title {
  width: 100%;
  justify-self: start;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color-secondary);
}

.toggle {
  position: relative;
  display: flex;
  gap: 1rem;
  flex-direction: row;
  width: fit-content;
  margin: 0 0 0 auto;
  border-radius: 1rem;
  height: 2rem;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  background: var(--toggle-bg);
}
@media (min-width: 40em) {
  .toggle {
    margin: 0;
  }
}
.toggle::before {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  z-index: 0;
  left: 0;
  transform: translateX(var(--toggle-button-pos));
  transition: transform 300ms ease-in-out;
}
.toggle__icon {
  fill: var(--text-color-secondary);
  height: 1rem;
  width: 1rem;
  z-index: 1;
  transition: var(--default-transistion);
}
.toggle__icon#icon-sun {
  fill: var(--icon-color-sun);
}
.toggle__icon#icon-moon {
  fill: var(--icon-color-moon);
}

.toggle__label {
  color: var(--text-color-secondary);
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.1;
  margin: 0;
  align-self: center;
}
@media (min-width: 40em) {
  .toggle__label {
    margin: 0 8px 0 auto;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5625rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  background: var(--card-bg);
  color: var(--text-color-secondary);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  overflow: hidden;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-bg-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), 0%, var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
  position: absolute;
  display: block;
  left: 0;
  top: 0;
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  height: 1.25rem;
  gap: 0.5625rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin: 0;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: normal;
  letter-spacing: 0.3125rem;
  color: var(--text-color-secondary);
  text-transform: uppercase;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, minmax(2.5rem, 2.5rem));
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  justify-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}

.footer {
  background: var(--background);
  color: var(--text);
  padding: 1.25rem;
  font-size: 0.8rem;
  text-align: center;
  border-top: 1px solid var(--text-color-secondary);
  width: 100%;
  height: 6.25rem;
  margin-top: auto;
}